.form{
    display: flex;
    flex-direction: column;
    gap: 24px;
    max-width: 400px;
}

.input{
    width: 100%;
    height: 30px;
    border-radius: 16px;
    padding: 0 16px;
}

.btn{
    max-width: 200px;
    border-radius: 16px;
    height: 30px;
    background-color: rgb(4, 56, 44);
    color: white;
    font-size: 16px;
    font-weight: 700;
    &:hover{
        background-color: rgb(6, 93, 58);
        cursor: pointer;
    }
}

.label{
    margin-top: 24px;
    margin-bottom: 12px;
    font-size: 18px;
}